@use "./sass/helpers/variables" as *;
@use "./sass/helpers/mixins";

// Start Landing
.landing {
    .main-post-box {
        box-shadow: $secondary-box-shadow;
        border-radius: $main-border-radius;
        overflow: hidden;
        cursor: pointer;
        img {
            width: 100%;
            max-width: 100%;
            height: 250px;
            @media (min-width: 768px) {
                height: 300px;
            }
            @media (min-width: 992px) {
                height: 400px;
            }
        }
    }
    .staff-picks {
        .staff-boxes {
            .staff-box {
                border-radius: $main-border-radius;
                cursor: pointer;
                transition: $main-transition;
                &:hover {
                    background-color: $alt-grey-color;
                }
                .post-category {
                    color: $blue-color;
                }
            }
        }
    }
}
// End Landing
// Start Overview
.overview {
    .row > div {
        padding-bottom: 0.75rem;
    }
    .read-more {
        border: 1px solid $blue-color !important;
        border-radius: 0;
        &:hover {
            background-color: #fff;
            color: $blue-color;
            border-color: $blue-color !important;
        }
    }
}
// End Overview